import React, { Component } from 'react';
import store from '../../redux/store';
import { Modal, Message, Button, Header, Icon, Image } from 'semantic-ui-react'
import { toggleErrorModal } from '../../redux/actions';
import TokenFromWei from '../../helpers/TokenFromWei';

class ConfirmBetModal extends Component {

    render() {
        let { open, tokensToBet, pickedTeam, teams } = this.props;

        const tokenList = tokensToBet.map((curr) => {
            return <li>{TokenFromWei(curr, 'symbol')}</li>
        });

        return (          
            <Modal size='tiny' open={open}>
                <Modal.Header>You are about to place a bet</Modal.Header>
                <Modal.Content>
                    <Image floated="left" bordered size='tiny' src={`/static/img/teams/${pickedTeam.slug}.png`} />
                    <Modal.Description>
                        <ul>{tokenList}</ul>
                        on {pickedTeam.name}                        
                    </Modal.Description>
                </Modal.Content>
                <Modal.Actions>
                    {/* <Button className="dark-orange-bg font-white" onClick={(e) => {this.setState({popupOpen: false})}}>
                        Close
                    </Button> */}
                </Modal.Actions>
            </Modal>        
        );
    }
}

export default ConfirmBetModal;